import React from 'react';
import PropTypes from 'prop-types';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import { formatNumberToLocale } from '../../../helpers/numberFormatter';
import { buyValue } from '../../../constants/sendOrder/orderEntryFormConstants';

import './RetailOrderOverviewComponent.css';

const classes = getBEMClasses('retail-order-overview');

const RetailOrderOverviewComponent = (props, context) => {
  const {
    symbol1,
    symbol2,
    price,
    transactionCost,
    notEnoughMarket,
    quantity,
    side,
    fee,
    feeProduct
  } = props;

  return (
    <div className={classes()}>
      <h3 className={classes('title')}>{context.t('Transaction Overview')}</h3>

      {notEnoughMarket ? (
        <div className={classes('content')}>
          <p className={classes('content')}>
            {context.t('We cannot fill this request.')}
          </p>
        </div>
      ) : (
        <div className={classes('content')}>
          <APLabelWithText
            customClass={classes()}
            label={context.t('Price')}
            text={context.t('{price} per {symbol1}', { price, symbol1 })}
          />

          <APLabelWithText
            customClass={classes()}
            label={context.t('Transaction Cost')}
            text={`${transactionCost} ${symbol2}`}
          />

          <APLabelWithText
            customClass={classes()}
            label={
              side === buyValue
                ? context.t('{symbol1} to Buy', { symbol1 })
                : context.t('{symbol1} to Sell', { symbol1 })
            }
            text={quantity}
          />

          <APLabelWithText
            name="fee"
            customClass={classes()}
            label={context.t('Fee')}
            text={`${formatNumberToLocale(
              fee,
              feeProduct && feeProduct.ProductSymbol
            )} ${feeProduct ? feeProduct.ProductSymbol : ''} `}
          />
        </div>
      )}
    </div>
  );
};

RetailOrderOverviewComponent.propTypes = {
  symbol1: PropTypes.string.isRequired,
  symbol2: PropTypes.string.isRequired,
  price: PropTypes.string.isRequired,
  transactionCost: PropTypes.string.isRequired,
  notEnoughMarket: PropTypes.bool.isRequired,
  quantity: PropTypes.string.isRequired,
  side: PropTypes.string.isRequired,
  fee: PropTypes.number.isRequired
};

RetailOrderOverviewComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailOrderOverviewComponent;



