import React from 'react';
import { connect } from 'react-redux';
import BigNumber from 'bignumber.js';
import { reduxForm, change, formValueSelector } from 'redux-form';
import RetailBuySellComponent from './RetailBuySellComponent';
import VerificationRequiredContainer from '../../VerificationRequired/VerificationRequiredContainer';
import { selectInstrument } from '../../../redux/actions/instrumentActions';
import {
  buyValue,
  sellValue,
  orderTypes
} from '../../../constants/sendOrder/orderEntryFormConstants';
import { productPositionAssetsSelector } from '../../../redux/selectors/productPositionSelectors';
import { valueForSelectedPriceSelector } from '../../../redux/selectors/buySellSelectors';
import {
  synchronizeQuantityWithPrice,
  synchronizePriceWithQuantity,
  clearBuySellForm
} from '../../../redux/actions/orderEntryActions';
import { MODAL_TYPES, openModal } from '../../../redux/actions/modalActions';
import {
  selectedInstrumentSelector,
  instrumentsWithProductFullNamesAndProductSymbols
} from '../../../redux/selectors/instrumentPositionSelectors';
import { kycVerificationLevelsSelector } from '../../../redux/selectors/kycLevelsSelectors';
import config from '../../../config';

const selector = formValueSelector('retailBuySell');

const RetailButSellComponentForm = reduxForm({
  form: 'retailBuySell',
  onSubmit: (payload, dispatch) => {
    dispatch(openModal(MODAL_TYPES.RETAIL_SUBMIT_ORDER));
  }
})(RetailBuySellComponent);

const mapStateToProps = state => {
  const positions = productPositionAssetsSelector(state);
  const { productManifest, position, form, level2 } = state;
  const formObj = form.retailBuySell || { values: {} };
  const { selectedProductId } = position;
  const { decimalPlaces } = state.product;
  const [customValues, ...dealValues] = valueForSelectedPriceSelector(
    state,
    formObj.values.side,
    formObj.values.price
  );
  const selectedInstrument = selectedInstrumentSelector(state);

  const { dealPrices } = config.RetailBuySell;
  const instruments = instrumentsWithProductFullNamesAndProductSymbols(
    state
  ).filter(instrument => dealPrices.hasOwnProperty(instrument.Symbol));
  const kycTooLow = kycVerificationLevelsSelector(state).disableTrading;

  const transactionCost =
    (formObj.values.amountRadio === 'custom'
      ? new BigNumber(formObj.values.price)
      : new BigNumber(formObj.values.amountRadio)) || 0;

  const selectedValue =
    dealValues.find(i => i.price === transactionCost.toNumber()) ||
    customValues;

  const disableTrading =
    kycTooLow || !selectedValue.limitPrice || !selectedValue.price;

  return {
    instruments,
    selectedInstrument,
    positions,
    level2,
    dealValues,
    selectedProductId,
    decimalPlaces,
    formObj,
    productManifest,
    initialValues: {
      instrument: selectedInstrument.InstrumentId,
      side: buyValue,
      amountRadio: selectedValue.price
        ? selectedValue.price.toString()
        : 'custom',
      price: '',
      quantity: '',
      fee: '0',
      limitPrice: transactionCost.toNumber() || '0',
      orderType: orderTypes.limit.displayName,
      responseMessage: ''
    },
    disableTrading,
    activeAmount: selector(state, 'amountRadio'),
    VerificationRequiredComponent: <VerificationRequiredContainer />
  };
};

const mapDispatchToProps = {
  setBuyValue: () => change('retailBuySell', 'side', buyValue),
  setSellValue: () => change('retailBuySell', 'side', sellValue),
  selectInstrument,
  synchronizeQuantityWithPrice,
  synchronizePriceWithQuantity,
  clearForm: clearBuySellForm,
  setAmount: amount => change('retailBuySell', 'amountRadio', amount)
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailButSellComponentForm);



