let getVariableFn = null;

export const getCSSVar = () => {
  if (!getVariableFn) {
    if (isCssVarsSupported()) {
      getVariableFn = getCssVariableFromDocumentBody;
    } else {
      getVariableFn = getCssVariable;
    }
  }

  return getVariableFn;
};

const parseCssVariable = (cssVarValue, rgbName, rgbModifier) => {
  if (cssVarValue.includes('rgb') || cssVarValue.includes('rgba')) {
    if (cssVarValue.includes(',')) {
      const nestedCssVar = cssVarValue.match(/(.*?)\(.*?\((.*?)\),(.*?)\)/); // extract value from "rgba(var(--nested-css-var), 0.75)"
      const rgbModifier = nestedCssVar[3];
      const rgbName = nestedCssVar[1];
      return getCssVariable(nestedCssVar[2], rgbName, rgbModifier);
    } else {
      const nestedCssVar = cssVarValue.match(/(.*?)\(.*?\((.*?)\)\)/); // extract value from "rgb(var(--nested-css-var))"
      const rgbName = nestedCssVar[1];
      return getCssVariable(nestedCssVar[2], rgbName);
    }
  } else if (cssVarValue.includes('var')) {
    const nestedCssVar = cssVarValue.match(/\((.*?)\)/)[1]; // extract value from "var(--nested-css-var)"
    if (rgbName || rgbModifier) {
      return getCssVariable(nestedCssVar, rgbName, rgbModifier);
    }
    return getCssVariable(nestedCssVar);
  }
};

export const getCssVariable = (value, rgbName = null, rgbModifier = null) => {
  const { cssVariables } = window;
  const supportedVars = ['rgb', 'rgba', 'var'];
  const cssVariable = cssVariables[value];

  const isSupportedVariable = supportedVars.some(
    item => cssVariable.indexOf(item) !== -1
  );

  if (isSupportedVariable) {
    return parseCssVariable(cssVariable, rgbName, rgbModifier);
  }

  if (rgbName && rgbModifier) {
    return `${rgbName}(${cssVariable},${rgbModifier})`;
  } else if (rgbName) {
    return `${rgbName}(${cssVariable})`;
  } else {
    return cssVariable;
  }
};

export const isCssVarsSupported = () => {
  return (
    window.CSS && window.CSS.supports && window.CSS.supports('(--foo: red)')
  );
};

export const getCssVariableFromDocumentBody = value =>
  getComputedStyle(document.body)
    .getPropertyValue(value)
    .trim();



