import React from 'react';
import PropTypes from 'prop-types';
import RetailBuySellContainer from './RetailBuySellContainer';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RetailMakeTransactionComponent.css';

const classes = getBEMClasses('retail-make-transaction');

const RetailMakeTransactionComponent = (props, context) => {
  return (
    <div className={classes()}>
      <h2 className={classes('title')}>{context.t('Make a Transaction')}</h2>

      <div className={classes('content')}>
        <RetailBuySellContainer />
      </div>
    </div>
  );
};

RetailMakeTransactionComponent.propTypes = {};

RetailMakeTransactionComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailMakeTransactionComponent;



