import { connect } from 'react-redux';
import get from 'lodash/get';
import RetailPriceChartComponent from './RetailPriceChartComponent';
import { changeTickersInterval } from '../../redux/actions/tickersActions';

const mapStateToProps = state => {
  const {
    instrument: { selectedInstrumentId, instruments },
    product: { decimalPlaces }
  } = state;
  const { Product2Symbol } = instruments.find(
    i => i.InstrumentId === selectedInstrumentId
  ) || { Product2Symbol: '' };

  const precision = get(decimalPlaces, Product2Symbol);
  return { precision };
};

const mapDispatchToProps = {
  changeTickersInterval
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailPriceChartComponent);



