import React from 'react';
import PropTypes from 'prop-types';
import ApexChartBuilder from 'apex-chart-react';
import 'apex-chart-react/lib/chart-style.css';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { getCSSVar } from '../../helpers/cssVarHelper';
import store from '../../redux/store';
import config from '../../config';
import '../../styles/components/TradeComponent.css';
import './RetailPriceChart.css';

const tradeComponentClasses = getBEMClasses('trade-component');

class RetailPriceChartComponent extends React.Component {
  renderChart() {
    const chartConfig = {
      store,
      changeTickersInterval: this.props.changeTickersInterval,
      currency_character: '',
      locale: config.global.locale,
      time_intervals: this.getTimeIntervalsConfig(),
      price_history: this.getPriceHistoryConfig(),
      defaults: this.getDefaultConfig(),
      date_format_months: [
        this.context.t('Jan', {}, 'January'),
        this.context.t('Feb', {}, 'February'),
        this.context.t('Mar', {}, 'March'),
        this.context.t('Apr', {}, 'April'),
        this.context.t('May', {}, 'May (the month)'),
        this.context.t('Jun', {}, 'June'),
        this.context.t('Jul', {}, 'July'),
        this.context.t('Aug', {}, 'August'),
        this.context.t('Sep', {}, 'September'),
        this.context.t('Oct', {}, 'October'),
        this.context.t('Nov', {}, 'November'),
        this.context.t('Dec', {}, 'December')
      ]
    };
    this.chart = new ApexChartBuilder('apex-chart-retail', chartConfig);
    this.chart.DrawChart();
  }

  getTimeIntervalsConfig() {
    return [
      {
        label: '5m',
        seconds: 300
      },
      {
        label: '15m',
        seconds: 900
      },
      {
        label: '30m',
        seconds: 1800
      },
      {
        label: '1h',
        seconds: 3600
      },
      {
        label: '1d',
        seconds: 86400
      }
    ];
  }

  getPriceHistoryConfig() {
    return {
      font_size: 10,
      font_face: this.cssVariable('--font__typeface'),
      border_primary_color: this.cssVariable('--chart__border-color-primary'),
      border_secondary_color: this.cssVariable('--table__row-separator-color'),
      background_color: this.cssVariable('--secondary__bg-color'),
      bullish_color: this.cssVariable('--chart__bullish-color'),
      bearish_color: this.cssVariable('--chart__bearish-color'),
      price_line_color: this.cssVariable('--chart__retail-price-line-color'),
      sma_line_color: 'transparent',
      ema_line_color: 'transparent',
      bollinger_band_upper_line_color: this.cssVariable(
        '--chart__bollinger-band-upper-color'
      ),
      bollinger_band_lower_line_color: this.cssVariable(
        '--chart__bollinger-band-lower-color'
      ),
      market_price_line_color: this.cssVariable(
        '--chart__market-price-line-color'
      ),
      market_price_label_color: this.cssVariable(
        '--chart__market-price-label-color'
      ),
      market_price_background_color: this.cssVariable(
        '--chart__market-price-bg-color'
      ),
      x_label_color: this.cssVariable('--font__color-secondary'),
      y_label_color: this.cssVariable('--font__color-secondary'),
      info_height: 0,
      info_precision: this.props.precision,
      y_label_width: 60,
      y_label_precision: this.props.precision,
      x_label_height: 30,
      x_label_precision: this.props.precision,
      info_labels: [
        this.context.t('Price'),
        this.context.t('Open'),
        this.context.t('Close'),
        this.context.t('High'),
        this.context.t('Low'),
        this.context.t('Volume')
      ],
      use_bg_grid_lines: true,
      grid_lines_dashed: true,
      grid_lines_color: this.cssVariable('--table__row-separator-color'),
      use_gradient: true,
      first_gradient_color: this.cssVariable('--chart__gradient-color-start'),
      second_gradient_color: this.cssVariable('--chart__gradient-color-end')
    };
  }

  getDefaultConfig() {
    return {
      font_size: 12,
      font_face: this.cssVariable('--font__typeface'),
      history_item_width: 20,
      history_item_margin: 0,
      history_interval: 86400,
      draw_simple_moving_average: false,
      draw_exponential_moving_average: false,
      draw_bollinger_bands: false,
      chart_type: 'line',
      order_book_depth_count: 20
    };
  }

  componentDidMount() {
    this.cssVariable = getCSSVar();
    this.renderChart();
  }

  componentWillUnmount() {
    if (this.chart) {
      this.chart.DestroyChart();
      this.chart = null;
    }
  }

  render() {
    const priceChartComponentClasses = getBEMClasses([
      'retail-price-chart',
      this.props.customClass
    ]);
    return (
      <div
        className={`${tradeComponentClasses()} ${priceChartComponentClasses()}`}>
        <div
          id="apex-chart-retail"
          className={`${priceChartComponentClasses('chart')}`}
        />
      </div>
    );
  }
}

RetailPriceChartComponent.propTypes = {
  precision: PropTypes.number
};

RetailPriceChartComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailPriceChartComponent;



