import { connect } from 'react-redux';
import RetailBalancesComponent from './RetailBalancesComponent';
import { getProductByIdSelector } from '../../../redux/selectors/productPositionSelectors';
import { selectedInstrumentSelector } from '../../../redux/selectors/instrumentPositionSelectors';

const mapStateToProps = state => {
  const selectedInstrument = selectedInstrumentSelector(state);

  const product1 = getProductByIdSelector(state, selectedInstrument.Product1);
  const product2 = getProductByIdSelector(state, selectedInstrument.Product2);

  return {
    product1,
    product2
  };
};

const mapDispatchToProps = {};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailBalancesComponent);



