import React from 'react';
import PropTypes from 'prop-types';
import Modal from '../../common/Modal/Modal';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import { formatDateTime } from '../../../helpers/dateHelper';
import { buyValue } from '../../../constants/sendOrder/orderEntryFormConstants';
import { getOrderFee } from '../../../helpers/orderHelper.js';
import './RetailOrderConfirmationModalComponent.css';

const bemClasses = getBEMClasses('retail-order-confirmation');

const RetailOrderConfirmationModalComponent = (props, context) => {
  const {
    buySellForm,
    isOpen,
    close,
    confirmReport,
    selectedInstrument,
    positions,
    price,
    quantity,
    finalQuantity,
    onClose
  } = props;

  const isBuySide = buySellForm.values.side === buyValue;
  const product = selectedInstrument.Product1Symbol;
  const responseMessage = buySellForm.values.responseMessage;

  return (
    <Modal
      isOpen={isOpen}
      title={context.t(
        `${
          isBuySide
            ? 'Buy ' + selectedInstrument.Product1Symbol
            : 'Sell ' + selectedInstrument.Product1Symbol
        }`
      )}
      close={() => {
        close();
        onClose();
      }}
      footer={{
        buttonText: context.t(getConfirmButtonText(isBuySide, responseMessage)),
        onClick: responseMessage
          ? () => {
              close();
              onClose();
            }
          : () => confirmReport(buySellForm.values),
        overrideClose: true
      }}
      customClass={bemClasses('modal')}>
      <div className={bemClasses()}>
        <h3 className={bemClasses('header')}>
          {responseMessage || context.t('Confirmation')}
        </h3>

        <APLabelWithText
          customClass={bemClasses('order-type')}
          label={context.t('Order Type')}
          text={context.t('Limit (Fill or Kill)')}
        />

        <APLabelWithText
          customClass={bemClasses()}
          label={
            isBuySide
              ? context.t('{product} to Buy', { product })
              : context.t('{product} to Sell', { product })
          }
          text={context.t(`${quantity}`)}
        />

        <APLabelWithText
          customClass={bemClasses()}
          label={context.t(`Limit Price`)}
          text={context.t(`${selectedInstrument.Product2Symbol} ${price}`)}
        />

        <APLabelWithText
          customClass={bemClasses()}
          label={context.t(`Fee`)}
          text={context.t(`${getOrderFee(buySellForm, positions)}`)}
        />

        <APLabelWithText
          customClass={bemClasses()}
          label={context.t(`Final Quantity`)}
          text={`${
            isBuySide
              ? selectedInstrument.Product1Symbol
              : selectedInstrument.Product2Symbol
          } ${finalQuantity}`}
        />

        <APLabelWithText
          customClass={bemClasses()}
          label={context.t('Date')}
          text={formatDateTime(new Date())}
        />
      </div>
    </Modal>
  );
};

RetailOrderConfirmationModalComponent.propTypes = {
  buySellForm: PropTypes.object.isRequired,
  close: PropTypes.func.isRequired,
  isOpen: PropTypes.bool.isRequired,
  confirmReport: PropTypes.func.isRequired,
  selectedInstrument: PropTypes.object.isRequired,
  positions: PropTypes.object.isRequired,
  price: PropTypes.object.isRequired,
  quantity: PropTypes.object.isRequired,
  finalQuantity: PropTypes.object.isRequired,
  onClose: PropTypes.func.isRequired
};

RetailOrderConfirmationModalComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailOrderConfirmationModalComponent;

const getConfirmButtonText = (isBuySide, responseMessage) => {
  // context.t('OK')
  if (responseMessage) {
    return 'OK';
  }

  // context.t('Confirm Buy Order')
  // context.t('Confirm Sell Order')
  return isBuySide ? 'Confirm Buy Order' : 'Confirm Sell Order';
};



