import React from 'react';
import PropTypes from 'prop-types';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RetailBalancesComponent.css';

const classes = getBEMClasses('retail-balances');

const RetailBalancesComponent = (props, context) => {
  const { product1, product2 } = props;

  return (
    <div className={classes()}>
      <h3 className={classes('title')}>{context.t('Balances')}</h3>

      <div className={classes('content')}>
        <APLabelWithText
          customClass={classes()}
          label={`${product1.ProductFullName}:`}
          text={product1.AvailableBalance}
        />

        <APLabelWithText
          customClass={classes()}
          label={`${product2.ProductFullName}:`}
          text={product2.AvailableBalance}
        />
      </div>
    </div>
  );
};

RetailBalancesComponent.propTypes = {
  product1: PropTypes.object.isRequired,
  product2: PropTypes.object.isRequired
};

RetailBalancesComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailBalancesComponent;



