import React from 'react';
import RetailPriceChartContainer from '../../RetailPriceChart/RetailPriceChartContainer';
import InstrumentTableContainer from '../../InstrumentTable/InstrumentTableContainer';
import APIcon from '../../common/APIcon/APIcon';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import './BuySellChartOverviewComponent.css';

const chartOverviewClasses = getBEMClasses('chart-overview');

const BuySellChartOverviewComponent = props => {
  const { product1, product2 } = props;
  return (
    <React.Fragment>
      <div className={chartOverviewClasses('chart-container')}>
        <div className={chartOverviewClasses('instrument-container')}>
          <div className={chartOverviewClasses('instrument-container-text')}>
            {product1}
            <div className={chartOverviewClasses('icon-container')}>
              <APIcon name="arrowNext" />
            </div>
            {product2}
          </div>
        </div>
        <RetailPriceChartContainer customClass="chart-overview" />
      </div>
      <div className={chartOverviewClasses('instrument-table')}>
        <InstrumentTableContainer customClass="chart-overview-instrument" />
      </div>
    </React.Fragment>
  );
};

export default BuySellChartOverviewComponent;



