import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../../common/APButton';
import APSelect from '../../common/APSelect/APSelect';
import APRadio from '../../common/APRadio/APRadio';
import APNumberInput from '../../common/APNumberInput';
import APIcon from '../../common/APIcon/APIcon';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import {
  buyValue,
  sellValue
} from '../../../constants/sendOrder/orderEntryFormConstants';
import { styleNames } from '../../../propTypes/commonComponent';
import './RetailBuySellComponent.css';
import RetailOrderConfirmationModalContainer from './RetailOrderConfirmationModalContainer';

const baseClasses = getBEMClasses('retail-buy-sell');

class RetailBuySellComponent extends React.Component {
  componentWillMount() {
    const selectedInstrument = this.props.instruments.find(instrument => {
      return (
        instrument.InstrumentId === this.props.selectedInstrument.InstrumentId
      );
    });

    if (selectedInstrument) return;

    const instrumentId = this.props.instruments[0].InstrumentId;
    this.props.selectInstrument(instrumentId);
  }

  getProducts = () => {
    const { positions, selectedInstrument } = this.props;

    const product1 = positions.find(
      item => item.ProductId === selectedInstrument.Product1
    );
    const product2 = positions.find(
      item => item.ProductId === selectedInstrument.Product2
    );

    return { product1, product2 };
  };

  getProductFullName = product => (product ? product.ProductFullName : '');

  getAvailableBalance = product => (product ? product.AvailableBalance : '');

  getButtonsText = (product1, product2, formObj) => {
    if (formObj.values.side === buyValue) {
      return this.context.t('Buy {product1} with {product2}', {
        product1: this.getProductFullName(product1),
        product2: this.getProductFullName(product2)
      });
    } else {
      return this.context.t('Sell {product1} for {product2}', {
        product1: this.getProductFullName(product1),
        product2: this.getProductFullName(product2)
      });
    }
  };

  render() {
    const {
      instruments,
      formObj,
      setBuyValue,
      setSellValue,
      selectInstrument,
      selectedInstrument,
      decimalPlaces,
      dealValues,
      synchronizeQuantityWithPrice,
      synchronizePriceWithQuantity,
      clearForm,
      handleSubmit,
      disableTrading,
      activeAmount,
      setAmount,
      VerificationRequiredComponent,
      submitting
    } = this.props;
    const { context } = this;
    const { product1, product2 } = this.getProducts();

    const buy = formObj.values.side === buyValue;
    const arrow = <p className={baseClasses('arrow')}>{buy ? '' : ''}</p>;
    const price =
      activeAmount === 'custom' ? formObj.values.price : +activeAmount;
    const quantity =
      activeAmount === 'custom'
        ? formObj.values.quantity
        : dealValues.filter(deal => deal.price === +activeAmount).quantity;
    const noBalance = buy
      ? this.getAvailableBalance(product2) < price
      : this.getAvailableBalance(product1) < quantity;

    return (
      <form className={baseClasses('container')} onSubmit={handleSubmit}>
        <div className={baseClasses('tab-wrapper')}>
          <div
            className={baseClasses('tab', {
              'buy-selected': buy
            })}
            onClick={() => {
              clearForm();
              setBuyValue();
            }}>
            <APIcon
              name="buy"
              classModifiers="big"
              customClass={baseClasses('icon')}
            />
            {context.t('Buy')}
          </div>
          <div className={baseClasses('vertical-divider')} />
          <div
            className={baseClasses('tab', {
              'sell-selected': formObj.values.side === sellValue
            })}
            onClick={() => {
              clearForm();
              setSellValue();
            }}>
            <APIcon
              name="sell"
              classModifiers="big"
              customClass={baseClasses('icon')}
            />
            {context.t('Sell')}
          </div>
        </div>

        <div className={baseClasses('crypto-container')}>
          <APSelect
            name="instrument"
            label={
              buy
                ? context.t('Choose a Crypto to Buy')
                : context.t('Choose a Crypto to Sell')
            }
            onSelect={instrumentId => {
              selectInstrument(parseInt(instrumentId, 10));
              clearForm();
            }}
            options={instruments.map(instrument => ({
              value: instrument.InstrumentId,
              label: `${instrument.Product1FullName} / ${
                instrument.Product2FullName
              }`
            }))}
            customClass={baseClasses()}
          />
        </div>

        <div className={baseClasses('amount-header')}>Amount</div>

        <div className={baseClasses('amount-container')}>
          {dealValues.map(deal => {
            return (
              <div
                className={baseClasses(
                  'amount-row',
                  deal.price === +activeAmount && 'active'
                )}
                key={deal.price}
                onClick={() => setAmount(deal.price.toString())}>
                <div className={baseClasses('first-in-row')}>
                  <APRadio
                    name="amountRadio"
                    id={deal.price.toString()}
                    value={deal.price.toString()}
                  />
                </div>
                <div>
                  {selectedInstrument.Product2Symbol === 'USD'
                    ? `$${deal.price}`
                    : `${selectedInstrument.Product2Symbol} ${deal.price}`}
                </div>
                {arrow}
                <div className={baseClasses('last-in-row')}>
                  {deal.quantity ? deal.quantity : '-'}
                </div>
              </div>
            );
          })}

          <div
            className={baseClasses('amount-row', 'last')}
            onClick={() => setAmount('custom')}>
            <div className={baseClasses('first-in-row')}>
              <APRadio name="amountRadio" id={'custom'} value={'custom'} />
            </div>
            <div>
              <APNumberInput
                name="price"
                customClass={baseClasses()}
                classModifiers={
                  activeAmount === 'custom' ? 'active' : 'disable'
                }
                decimalPlaces={decimalPlaces[selectedInstrument.Product2Symbol]}
                customChange={value => {
                  synchronizeQuantityWithPrice(
                    value,
                    'retailBuySell',
                    'quantity',
                    formObj
                  );
                }}
              />
            </div>
            {arrow}
            <div className={baseClasses('last-in-row')}>
              <APNumberInput
                name="quantity"
                customClass={baseClasses()}
                classModifiers={
                  activeAmount === 'custom' ? 'active' : 'disable'
                }
                decimalPlaces={decimalPlaces[selectedInstrument.Product1Symbol]}
                customChange={value => {
                  synchronizePriceWithQuantity(
                    value,
                    'retailBuySell',
                    'price',
                    formObj
                  );
                }}
              />
            </div>
          </div>
          <RetailOrderConfirmationModalContainer />
        </div>

        <div className={baseClasses('confirm-button-container')}>
          <APButton
            customClass={baseClasses()}
            onClick={handleSubmit}
            styleName={
              formObj.values.side === sellValue
                ? styleNames.subtractive
                : styleNames.additive
            }
            disabled={disableTrading || noBalance}>
            {submitting
              ? context.t('Processing...')
              : this.getButtonsText(product1, product2, formObj)}
          </APButton>

          {VerificationRequiredComponent}
        </div>
      </form>
    );
  }
}

RetailBuySellComponent.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  formObj: PropTypes.object.isRequired,
  setBuyValue: PropTypes.func.isRequired,
  setSellValue: PropTypes.func.isRequired,
  dealValues: PropTypes.array.isRequired,
  synchronizeQuantityWithPrice: PropTypes.func.isRequired,
  synchronizePriceWithQuantity: PropTypes.func.isRequired,
  clearForm: PropTypes.func.isRequired,
  selectInstrument: PropTypes.func.isRequired,
  selectedInstrument: PropTypes.object.isRequired,
  decimalPlaces: PropTypes.object.isRequired,
  instruments: PropTypes.array.isRequired,
  disableTrading: PropTypes.bool.isRequired,
  submitting: PropTypes.bool.isRequired,
  VerificationRequiredComponent: PropTypes.element
};

RetailBuySellComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailBuySellComponent;



