import { connect } from 'react-redux';
import modalDecorator from '../../../hocs/modalDecorator';
import BigNumber from 'bignumber.js';
import { MODAL_TYPES } from '../../../redux/actions/modalActions';
import RetailOrderConfirmationModalComponent from './RetailOrderConfirmationModalComponent';
import { valueForSelectedPriceSelector } from '../../../redux/selectors/buySellSelectors';
import { buyValue } from '../../../constants/sendOrder/orderEntryFormConstants';
import {
  placeOrder,
  clearBuySellForm
} from '../../../redux/actions/orderEntryActions';
import { orderFormTypes } from '../../../constants/sendOrder/orderFormTypes';
import { truncateToDecimals } from '../../../helpers/numberFormatter';
import { selectedInstrumentSelector } from '../../../redux/selectors/instrumentPositionSelectors';

const mapStateToProps = state => {
  const {
    form,
    position: { positions },
    product: { decimalPlaces }
  } = state;
  const buySellForm = form.retailBuySell || { values: { fee: {} } };
  const dealValues = valueForSelectedPriceSelector(
    state,
    buySellForm.values.side
  );

  const price =
    (buySellForm.values.amountRadio === 'custom'
      ? new BigNumber(buySellForm.values.price)
      : new BigNumber(buySellForm.values.amountRadio)) || 0;

  const selectedValue = dealValues.find(i => i.price === price.toNumber()) || {
    quantity: {}
  };

  const quantity =
    (buySellForm.values.amountRadio === 'custom'
      ? new BigNumber(buySellForm.values.quantity)
      : new BigNumber(selectedValue.quantity)) || 0;

  const selectedInstrument = selectedInstrumentSelector(state);
  const {
    Product1Symbol,
    Product2Symbol,
    Product1,
    Product2
  } = selectedInstrument;
  const isBuySide = buySellForm.values.side === buyValue;
  const finalQuantitySymbol = isBuySide ? Product1Symbol : Product2Symbol;
  const finalQuantityProduct = isBuySide ? Product1 : Product2;
  let finalQuantity = isBuySide ? quantity : price;

  if (finalQuantityProduct === buySellForm.values.fee.ProductId) {
    finalQuantity -= buySellForm.values.fee.OrderFee;
  }

  return {
    positions,
    buySellForm,
    selectedInstrument,
    price,
    quantity,
    finalQuantity: truncateToDecimals(
      finalQuantity,
      decimalPlaces[finalQuantitySymbol]
    )
  };
};

const mapDispatchToProps = dispatch => {
  return {
    confirmReport: form => dispatch(placeOrder(orderFormTypes.buySell, form)),
    onClose: () => dispatch(clearBuySellForm())
  };
};

const container = connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailOrderConfirmationModalComponent);

export default modalDecorator({
  name: MODAL_TYPES.RETAIL_SUBMIT_ORDER
})(container);



