import { connect } from 'react-redux';
import BigNumber from 'bignumber.js';
import RetailOrderOverviewComponent from './RetailOrderOverviewComponent';
import { selectedInstrumentSelector } from '../../../redux/selectors/instrumentPositionSelectors';
import { valueForSelectedPriceSelector } from '../../../redux/selectors/buySellSelectors';
import { formatNumberToLocale } from '../../../helpers/numberFormatter';

const mapStateToProps = state => {
  const {
    form: { retailBuySell },
    position: { positions }
  } = state;

  const {
    price,
    amountRadio,
    quantity: formQuantity,
    fee,
    side
  } = retailBuySell ? retailBuySell.values : { fee: {} };
  const { Product1Symbol, Product2Symbol } = selectedInstrumentSelector(state);
  const [customValue, ...dealValues] = valueForSelectedPriceSelector(
    state,
    side,
    price
  );

  const transactionCost = new BigNumber(
    amountRadio === 'custom' ? price : amountRadio
  );

  const selectedValue =
    dealValues.find(i => i.price === transactionCost.toNumber()) || customValue;

  const quantity = new BigNumber(
    amountRadio === 'custom' ? formQuantity : selectedValue.quantity
  );

  const { limitPrice } = selectedValue;

  return {
    symbol1: Product1Symbol,
    symbol2: Product2Symbol,
    side,
    positions,
    feeProduct: positions[fee.ProductId],
    fee: fee.OrderFee || 0,
    price: formatNumberToLocale(limitPrice, Product2Symbol),
    notEnoughMarket: !limitPrice,
    transactionCost: formatNumberToLocale(transactionCost, Product2Symbol),
    quantity: formatNumberToLocale(quantity.toString(), Product1Symbol)
  };
};

const RetailOrderOverviewContainer = connect(mapStateToProps)(
  RetailOrderOverviewComponent
);

export default RetailOrderOverviewContainer;



