import { connect } from 'react-redux';
import BuySellChartOverviewComponent from './BuySellChartOverviewComponent';
import { selectedInstrumentSelector } from '../../../redux/selectors/instrumentPositionSelectors';

const mapStateToProps = state => {
  const selectedInstrument = selectedInstrumentSelector(state);

  const product1 = selectedInstrument.Product1Symbol;
  const product2 = selectedInstrument.Product2Symbol;

  return {
    product1,
    product2
  };
};

export default connect(
  mapStateToProps,
  null
)(BuySellChartOverviewComponent);



