import { connect } from 'react-redux';
import InstrumentTableComponent from './InstrumentTableComponent';
import { selectedInstrumentSelector } from '../../redux/selectors/instrumentPositionSelectors';

const mapStateToProps = state => {
  const { level1, instrument } = state;
  const { selectedInstrumentId } = instrument;

  return {
    level1: level1[selectedInstrumentId],
    selectedInstrument: selectedInstrumentSelector(state)
  };
};

export default connect(
  mapStateToProps,
  null
)(InstrumentTableComponent);



