import React from 'react';
import PropTypes from 'prop-types';
import {
  formatPercentageValue,
  formatNumberToLocale
} from '../../helpers/numberFormatter';
import * as level1DataProp from '../../propTypes/level1Data';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { instrumentPropType } from '../../propTypes/instrumentPropTypes';

import './InstrumentTableComponent.css';

const InstrumentTableComponent = (
  { level1, selectedInstrument, customClass },
  context
) => {
  const baseClasses = getBEMClasses(['instrument-table', customClass]);

  const {
    LastTradedPx,
    Rolling24HrPxChange,
    BestBid,
    BestOffer,
    Rolling24HrVolume,
    SessionLow,
    SessionHigh
  } = level1;
  const productSymbol = selectedInstrument
    ? selectedInstrument.Product1Symbol
    : '';
  const priceSymbol = selectedInstrument
    ? selectedInstrument.Product2Symbol
    : '';

  return (
    <div className={baseClasses()}>
      {customClass === 'market-overview-instrument' && (
        <div className={baseClasses('row')}>
          <span className={baseClasses('title')}>
            {context.t(`${productSymbol}${priceSymbol} Price Data:`)}
          </span>
        </div>
      )}
      <div className={baseClasses('row')}>
        <span className={baseClasses('value', 'last-price')}>
          {formatNumberToLocale(LastTradedPx, priceSymbol)}
        </span>
        <span className={baseClasses('title')}>{context.t('Last Price')}</span>
      </div>

      <div className={baseClasses('row')}>
        <span
          className={baseClasses(
            'value',
            Rolling24HrPxChange >= 0 ? '24h-positive' : '24h-negative'
          )}>
          {formatPercentageValue(Rolling24HrPxChange)}
        </span>
        <span className={baseClasses('title')}>
          {context.t('24 Hour Pct Chg')}
        </span>
      </div>

      <div className={baseClasses('row', 'hideable')}>
        <span className={baseClasses('value', 'bid')}>
          {formatNumberToLocale(BestBid, priceSymbol)}
        </span>
        <span className={baseClasses('title')}>{context.t('Bid')}</span>
      </div>

      <div className={baseClasses('row', 'hideable')}>
        <span className={baseClasses('value', 'ask')}>
          {formatNumberToLocale(BestOffer, priceSymbol)}
        </span>
        <span className={baseClasses('title')}>{context.t('Ask')}</span>
      </div>

      <div className={baseClasses('row')}>
        <span
          className={baseClasses(
            'value',
            '24h-volume'
          )}>{`${formatNumberToLocale(
          Rolling24HrVolume,
          2
        )} ${productSymbol}`}</span>
        <span className={baseClasses('title')}>
          {context.t('24 Hour Volume')}
        </span>
      </div>

      <div className={baseClasses('row', 'hideable')}>
        <span className={baseClasses('value', '24h-low')}>
          {formatNumberToLocale(SessionLow, priceSymbol)}
        </span>
        <span className={baseClasses('title')}>{context.t('24 Hour Low')}</span>
      </div>

      <div className={baseClasses('row', 'hideable')}>
        <span className={baseClasses('value', '24h-high')}>
          {formatNumberToLocale(SessionHigh, priceSymbol)}
        </span>
        <span className={baseClasses('title')}>
          {context.t('24 Hour High')}
        </span>
      </div>
    </div>
  );
};

InstrumentTableComponent.propTypes = {
  level1: level1DataProp.type.isRequired,
  selectedInstrument: instrumentPropType
};

InstrumentTableComponent.defaultProps = {
  level1: level1DataProp.defaultProps
};

InstrumentTableComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default InstrumentTableComponent;



